<template>
  <div v-loading="loading">
    <!---->
    <div>
      <a-input-search
          prefix="#"
          placeholder=""
          enter-button=""
          size="large"
          @search="onSearch"
          v-model="searchInput"
          style="width: 20vw;"
          @keyup="upperCase"
      />
    </div>
    <!---->
    <h1 style="font-size: 35px;margin: 20px 0 10px 0">{{playName}}</h1>
    <!---->
    <el-tabs v-if="this.playName" >
      <el-tab-pane label="">
        <!---->
        <div style="background: #ECECEC; padding: 10px">
          <!---->
          <a-row :gutter="10">
            <!---->
            <a-col :span="6" >
              <a-card :hoverable='true'>
                <template slot="title">
                  <div class="cardTitle">
                    <img src="~@/assets/icon_trophy_medium.png" alt="" style="width: 2vw;">
                    <span></span>
                  </div>
                </template>
                <a-statistic
                    :value="trophies"
                    :value-style="{ color: '#cf1322', fontSize: '40px' }"
                >
                  <template #suffix>
                    <a-icon :type="trophies < highestTrophies? 'arrow-down' : 'arrow-up'" style="font-size: 1.2em;"/>
                    <span style="font-size: 1.2em">({{ highestTrophies - trophies  }})</span>
                  </template>
                </a-statistic>
              </a-card>
            </a-col>
            <!---->
            <a-col :span="6">
              <a-card :hoverable='true'>
                <template slot="title">
                  <div class="cardTitle">
                    <img src="~@/assets/icon_trophy_medium.png" alt="" style="width: 2vw;">
                    <span></span>
                  </div>
                </template>
                <a-statistic
                    :value="highestTrophies"
                    :value-style="{ color: '#cf1322', fontSize: '40px' }"
                >
                </a-statistic>
              </a-card>
            </a-col>
            <!---->
            <a-col :span="6">
              <a-card :hoverable='true'>
                <template slot="title">
                  <div class="cardTitle">
                    <img src="~@/assets/icon_player_level.png" alt="" style="width: 1.5vw;">
                    <span></span>
                  </div>
                </template>
                <a-statistic
                    :value="expLevel"
                    :value-style="{ color: '#cf1322', fontSize: '40px' }"
                >
                </a-statistic>
              </a-card>
            </a-col>
            <!---->
            <a-col :span="6">
              <a-card :hoverable='true'>
                <template slot="title">
                  <div class="cardTitle">
                    <img src="~@/assets/icon_championship.png" alt="" style="width: 20px;">
                    <span></span>
                  </div>
                </template>
                <a-statistic
                    :value="isQualifiedFromChampionshipChallenge?'':''"
                    :value-style="{ color: '#cf1322', fontSize: '40px' }"
                >
                </a-statistic>
              </a-card>
            </a-col>
          </a-row>
          <!---->
          <a-row :gutter="10" style="margin-top: 10px">
            <!--3v3-->
            <a-col :span="8">
              <a-card :hoverable='true'>
                <template slot="title">
                  <div class="cardTitle">
                    <img src="~@/assets/icon_friends.png" alt="" style="width: 2.2vw;">
                    <span>3V3</span>
                  </div>
                </template>
                <a-statistic
                    :value="threeVsthreeVictories"
                    :value-style="{ color: '#cf1322', fontSize: '40px' }"
                >
                </a-statistic>
              </a-card>
            </a-col>
            <!---->
            <a-col :span="8">
              <a-card :hoverable='true'>
                <template slot="title">
                  <div class="cardTitle">
                    <img src="~@/assets/Vector Smart Object-7.png" alt="" style="width: 1.5vw;">
                    <span></span>
                  </div>
                </template>
                <a-statistic
                    :value="soloVictories"
                    :value-style="{ color: '#cf1322', fontSize: '40px' }"
                >
                </a-statistic>
              </a-card>
            </a-col>
            <!---->
            <a-col :span="8">
              <a-card :hoverable='true'>
                <template slot="title">
                  <div class="cardTitle">
                    <img src="~@/assets/icon_showdownplus.png" alt="" style="width: 1.5vw;">
                    <span></span>
                  </div>
                </template>
                <a-statistic
                    :value="duoVictories"
                    :value-style="{ color: '#cf1322', fontSize: '40px' }"
                >
                </a-statistic>
              </a-card>
            </a-col>
          </a-row>
          <!---->
          <a-row :gutter="10" style="margin-top: 10px">
            <a-col :span="24">
              <a-card :hoverable='true'>
                <template slot="title">
                  <div class="cardTitle">
                    <img src="~@/assets/icon_club_league.png" alt=" "  style="width: 2vw;">
                    <span></span>
                  </div>
                </template>
                <a-statistic
                    :value="club+' ('+clubTag+')'"
                    :value-style="{ color: '#cf1322', fontSize: '40px' }"
                >
                </a-statistic>
              </a-card>
            </a-col>
          </a-row>
        </div>
      </el-tab-pane>
      <el-tab-pane label="">
        <a-table
            bordered
            :columns="columns"
            :data-source="brawlers"
            :pagination="false"
            :scroll="{ y: 800 }"
        >
          <span slot="id" slot-scope="id">
            <img :src="require('../../assets/brawlers/'+ id +'.png')" alt="" width="60px">
          </span>
        </a-table>
      </el-tab-pane>
    </el-tabs>



  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "BSPersonalCard",
  data(){
    return{
      loading: false,
      searchInput: '',  //
      playerInfo: {},
      playName: '', //
      trophies: '', //
      highestTrophies: '', //
      expLevel: '',  //
      isQualifiedFromChampionshipChallenge: '',
      threeVsthreeVictories: '',
      soloVictories: '',
      duoVictories: '',
      club: '', //
      clubTag: '',  //
      brawlers: [],  //
      columns: [
        {
          title: '',
          dataIndex: 'id',
          key: 'id',
          scopedSlots: { customRender: 'id' },
          width: '100px'
        },
        {
          title: '',
          dataIndex: 'power',
          key: 'power',
          sorter: (a, b) => a.power - b.power,
          width: '120px'
        },
        {
          title: '',
          key: 'rank',
          dataIndex: 'rank',
          sorter: (a, b) => a.rank - b.rank,
          width: '140px'

        },
        {
          title: '',
          key: 'trophies',
          dataIndex: 'trophies',
          sorter: (a, b) => a.trophies - b.trophies,
          width: '160px'

        },
        {
          title: '',
          key: 'highestTrophies',
          dataIndex: 'highestTrophies',
          sorter: (a, b) => a.highestTrophies - b.highestTrophies,
        },
      ],
    }
  },
  methods: {
    //
    upperCase(){
      this.searchInput = this.searchInput.toUpperCase()
    },
    //
    onSearch(){
      if(this.searchInput === ''){
        alert('')
      }
      else{
        this.loading = true
        axios({
          methods: 'GET',
          url: '/playStatsApi/' + this.searchInput,
        }).then((res) => {
          this.playName = '@' + res.data.name
          this.trophies = res.data.trophies
          this.highestTrophies = res.data.highestTrophies
          this.expLevel = res.data.expLevel
          this.isQualifiedFromChampionshipChallenge = res.data.isQualifiedFromChampionshipChallenge
          this.threeVsthreeVictories = res.data['3vs3Victories']
          this.soloVictories = res.data.soloVictories
          this.duoVictories = res.data.duoVictories
          this.club = res.data.club.name
          this.clubTag = res.data.club.tag
          this.brawlers = res.data.brawlers
          this.loading = false
        })
      }
    }
  }
}
</script>

<style>
/**/
img{
  image-rendering: -moz-crisp-edges;
  image-rendering: -o-crisp-edges;
  image-rendering: -webkit-optimize-contrast;
  image-rendering: crisp-edges;
  -ms-interpolation-mode: nearest-neighbor;
}
/**/
.cardTitle span{
  display: inline-block;
  vertical-align: middle;
  margin-left: 5px;
  font-family: '',serif;
  font-size: 1.2em;

}
</style>
